body {
    background: #FAF3EC;
    font-family: 'Roboto';
}

::-webkit-input-placeholder {
    /* Chrome/Opera/Safari */
    color: #748194;
}

::-moz-placeholder {
    /* Firefox 19+ */
    color: #748194;
}

:-ms-input-placeholder {
    /* IE 10+ */
    color: #748194;
}

:-moz-placeholder {
    /* Firefox 18- */
    color: #748194;
}

.login-container {
    display: none;
    position: absolute;
    width: auto;
    height: auto;
    top: calc(50% - 240px);
    left: calc(40% - 160px);
    border-radius: 15px 15px 15px 15px;
}

.login-c1 {
    box-shadow: 0 0 10px grey;
    background-color: white;
    width: 300px;
    height: 500px;
    display: inline-block;
    border-radius: 15px 15px 15px 15px;
}

.c11 {
    background-image: url('./logo.PNG');
    background-size: 300px 300px;
    background-repeat: no-repeat;
    background-color: #364f6b;
    background-position-y: center;
    width: 300px;
    height: 400px;
    display: inline-block;
    position: absolute;
    z-index: 4;
    border-radius: 15px 15px 200px 200px;
}

#left,
#right {
    color: white;
    display: inline-block;
    width: 146px;
    height: 500px;
    background-color: white;
    cursor: pointer;
}

#left {
    border-radius: 15px 0px 0px 15px;
}

#right {
    border-radius: 15px 15px 15px 0px;
}

.left_hover {
    color: #53adcb;
    box-shadow: 5px 0 18px -10px #333;
    z-index: 1;
    position: absolute;
}

.vertical_center {
    margin-top: auto;
    margin-bottom: auto;
}

.right_hover {
    box-shadow: -5px 0 15px -10px #333;
    z-index: 1;
    position: absolute;
}

.login-s1class {
    color: #748194;
    position: absolute;
    bottom: 0;
    left: 63%;
    margin-left: -50%;
}

.login-s1class span,
.login-s2class span {
    display: block;
}

.su {
    font-size: 20px;
}

.login-s2class {
    color: #748194;
    position: absolute;
    bottom: 0;
    right: 63%;
    margin-right: -50%;
}

.mainhead {
    color: white;
    font-size: 30px;
    text-align: center;
    margin-top: 50px;
}

.mainp {
    color: white;
    font-size: 16px;
    text-align: center;
